#cc-slider {
  text-align: center;
  margin: 0 auto;
  width: 100%; }

#cc-slider input {
  display: none; }

#cc-slider label a {
  color: white;
  cursor: pointer;
  text-decoration: none; }

#cc-slider label:hover {
  color: #ff0042 !important; }

#slide1:checked ~ #cc-slides .inner {
  margin-left: 0; }

#slide2:checked ~ #cc-slides .inner {
  margin-left: -100%; }

#slide3:checked ~ #cc-slides .inner {
  margin-left: -200%; }

#slide4:checked ~ #cc-slides .inner {
  margin-left: -300%; }

#slide5:checked ~ #cc-slides .inner {
  margin-left: -400%; }

#overflow {
  width: 100%;
  overflow: hidden; }

#cc-slides article img {
  width: 100%; }

#cc-slides .inner {
  width: 500%;
  line-height: 0; }

#cc-slides article {
  position: relative;
  width: 20%;
  float: left; }

#controls {
  position: relative;
  margin: -8.5% 0 0 80.5%;
  width: 15%;
  height: 50px; }
  @media only screen and (min-width: 1024px) {
    #controls {
      margin: -10% 0 0 80.5%; } }
  @media only screen and (min-width: 1280px) {
    #controls {
      margin: -10% 0 0 80.5%; } }
  @media only screen and (min-width: 1366px) {
    #controls {
      margin: -6.5% 0 0 80.5%;
      width: 13%; } }

#controls label {
  display: none;
  width: 50px;
  height: 50px; }

#active {
  margin: 23% 0 0;
  text-align: center; }

#active label {
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  display: inline-block;
  width: 10px;
  height: 10px;
  background: #133b55; }

#active label:hover {
  background: #ccc;
  border-color: #5d5d5d !important; }

#controls label:hover {
  opacity: 0.8; }

#slide1:checked ~ #controls label:nth-child(2),
#slide2:checked ~ #controls label:nth-child(3),
#slide3:checked ~ #controls label:nth-child(4),
